.burgerMenu {
  width: 35px;
  height: 35px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 2;
}

.burgerMenu span {
  display: block;
  position: absolute;
  height: 4px;
  width: 80%;
  background:  #6F7F95;
  border-radius: 9px;
  opacity: 1;
  left: 3px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.burgerMenu span:nth-child(1) {
  top: 5px;
}

.burgerMenu span:nth-child(2),.burgerMenu span:nth-child(3) {
  top: 15px;
}

.burgerMenu span:nth-child(4) {
  top: 25px;
}

.burgerMenu.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.burgerMenu.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.burgerMenu.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.burgerMenu.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}
